// 
const themes = {
    light: {
        '--primary-color': '#3498db',
        '--secondary-background': '#f8f9ff',  //  --ZiBaiBgc
        '--active-background': '#f7f7f8',     //  --ActiveBgc
        '--background-color': '#ffffff',
        '--border-color': '#f0f1fb',          //  --Border
        '--text-color': '#000000',
        '--text-color-secondary': '#666666',  // 
        '--button-color': '#1988fa',          //  --active-background-color
        '--button-color-rgb': '25, 136, 250', // RGB
        '--card-background': '#f5f5f5',       //  --article-card-background-color
        '--gradient-primary': 'linear-gradient(to right, #1988fa 0%, #33c4f9 50%, #00f2fe 100%)',  //  --Business-card-gradient
        '--shadow-color': 'rgba(0, 0, 0, 0.1)',  // 
        '--shadow-color-strong': 'rgba(0, 0, 0, 0.15)'  // 
    },
    dark: {
        '--primary-color': '#808080',
        '--secondary-background': '#1a1a1a',
        '--active-background': '#2a2a2a',
        '--background-color': '#121212',
        '--border-color': '#333333',
        '--text-color': '#e0e0e0',
        '--text-color-secondary': '#999999',  // 
        '--button-color': '#666666',
        '--button-color-rgb': '102, 102, 102', // RGB
        '--card-background': '#1f1f1f',
        '--gradient-primary': 'linear-gradient(to right, #4a4a4a 0%, #666666 50%, #808080 100%)',
        '--shadow-color': 'rgba(255, 255, 255, 0.05)',  // 
        '--shadow-color-strong': 'rgba(255, 255, 255, 0.08)'  // 
    }
};

// 
class ThemeManager {
    constructor() {
        if (!themes || !themes.light || !themes.dark) {
            throw new Error('');
        }
        this.themes = themes;
        const savedTheme = localStorage.getItem('theme');
        const prefersDark = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
        this.currentTheme = savedTheme || (prefersDark ? 'dark' : 'light');
        this.initTheme();
        
        // 
        window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', e => {
            if (!localStorage.getItem('theme')) {
                this.currentTheme = e.matches ? 'dark' : 'light';
                this.applyTheme(this.currentTheme);
            }
        });
    }

    // 
    initTheme() {
        this.applyTheme(this.currentTheme);
    }

    // 
    toggleTheme() {
        this.currentTheme = this.currentTheme === 'light' ? 'dark' : 'light';
        this.applyTheme(this.currentTheme);
        localStorage.setItem('theme', this.currentTheme);
        return this.currentTheme;
    }

    // 
    applyTheme(themeName) {
        const theme = themes[themeName];
        for (const [key, value] of Object.entries(theme)) {
            document.documentElement.style.setProperty(key, value);
        }
    }

    // 
    getCurrentTheme() {
        return this.currentTheme;
    }
}

export default new ThemeManager();