import Vue from 'vue'
import store from '../store'
import ElementUI from 'element-ui'
// ElementUI
import 'element-ui/lib/theme-chalk/index.css'
// ElementUI

import Index from '../views/index.vue'
// index.vue
import Login from '../views/Login.vue'
import Home from '../components/IndeHome.vue'
import Discover from "../components/IndexCategories.vue"
// import Settings from '../components/IndexSettings.vue'//
import VueRouter from 'vue-router'
import Article from '../views/Article.vue'
import My from '../components/User.vue';
import OtherUser from '../components/OtherUser.vue';

import { isTokenExpired } from '@/utils/auth';
import { Notification } from 'element-ui';

// ElementUI
Vue.use(ElementUI)
// VueRouter
Vue.use(VueRouter)
// 
// 
const routes = [
    
    // Index
    {
        path: '/',
        redirect: '/Index',
    },
    // Index
    {
        path: '/Index',
        component: Index,
        name: 'Index',
        meta: { requiresAuth: true }, // 
        children: [
            {
                path: '', // 
                redirect: 'home' //  home
            },
            {
                path: 'home', // 
                component: Home,
                name: 'Home',
                meta: { requiresAuth: true }, // 
            },
            {
                path: 'discover',
                component: Discover,
                name: "Discover",
                meta: { requiresAuth: true }, // 
            },

            {
                path: 'my', // 
                component: My,
                name: 'User',
                meta: { requiresAuth: true }, // 
            },
           
            // {
            //     path: 'settings',
            //     component: Settings,
            //     name: "Settings",
            //     meta: { requiresAuth: true }, // 

            // },
        ]
    },

    // Entry

    {
        path: '/Login',
        component: Login,
        name: 'Login'
    },

    {
        path: '/Article/:id', // :id 
        name: 'Article',
        component: Article,
        meta: { requiresAuth: true }, // 
    },
    {
        path: '/user/:id', // 
        name: 'OtherUser',
        component: OtherUser,
        meta: { requiresAuth: true }, // 
    }
]

const router = new VueRouter({
    mode: 'history', //  history 
    routes,
})

// 
router.beforeEach((to, from, next) => {
    if (to.name === 'Article') {
        // localStorage
        localStorage.setItem('articleFrom', from.fullPath);
    }
    next();
})
// 
router.beforeEach((to, from, next) => {
    const token = localStorage.getItem("token");


    //  token 
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!token || isTokenExpired()) {

            Notification.error({
                title: "",
                message: "",
                duration: 3000,
            });
            localStorage.removeItem("token"); //  token
            next('/Login'); //  token 
        } else {
            next(); // token 
        }
    } else {
        next(); // 
    }
});
export default router
