import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
//  Vuex 
Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        // localStorage
        user: {
            token: localStorage.getItem('token') || null,
            details: JSON.parse(localStorage.getItem('userDetails')) || {}
        },
        // 
        sharedData: null,
        hasVisitedHome: localStorage.getItem('hasVisitedHome') === 'true',  // localStorage
        favorites: [], // 
        userId: null,   //  ID
        articleFrom: null // 
    },

    mutations: {
        // 
        setSharedData(state, data) {
            state.sharedData = data; //  data  sharedData 
        },
        // 
        setArticleFrom(state, path) {
            state.articleFrom = path;
        },

        // 
        setUser(state, { token, details }) {
            //  token  user details  Vuex 
            state.user.token = token;
            state.user.details = details;
            // localStorage
            if (token) {
                localStorage.setItem('token', token);
                localStorage.setItem('userDetails', JSON.stringify(details));
            } else {
                localStorage.removeItem('token');
                localStorage.removeItem('userDetails');
            }
        },

        // 
        deleteUser(state) {
            state.user = {
                token: null, //  token
                details: {} // 
            };
            // localStorage
            localStorage.removeItem('token');
            localStorage.removeItem('userDetails');
        },
        setHasVisitedHome(state, status) {
            state.hasVisitedHome = status; // 
            localStorage.setItem('hasVisitedHome', status); // localStorage
        },
        updateUsername(state, newUsername) {
            state.user.details.username = newUsername;
        },
        // 
        setFavorites(state, favorites) {
            state.favorites = favorites;

            
        },
    },

    actions: {
        //  mutation
        updateSharedData({ commit }, data) {
            commit('setSharedData', data); //  mutation  sharedData
        },

        //  mutation
        updateUser({ commit }, { token, details }) {

            //  token  details
            commit('setUser', { token, details }); //  mutation 
        },

        // 
        logout({ commit }) {
            commit('deleteUser'); //  mutation 
        },
        markVisitedHome({ commit }) {
            commit('setHasVisitedHome', true); // 
        },
        resetVisitedHome({ commit }) {
            commit('setHasVisitedHome', false); // 
        },
        // 
        async VuexloadFavorites({ commit }) {
            const token = localStorage.getItem('token');
            if (!token) {
                return;
            }

            try {
                // 
                const response = await axios.get('/api/user', {
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                });

                const userId = response.data._id;  //  ID
               

                // 
                const favoritesResponse = await axios.get(`/api/favorites/${userId}`);
                const favorites = favoritesResponse.data;   

                commit('setFavorites', favorites);  //  Vuex 

                
            } catch (error) {
                console.error(':', error);
            }
        },
    },

    getters: {
        //  token 
        isLoggedIn(state) {

            return !!state.user.token; //  token  true false
        },

        // 
        getUser(state) {
            return state.user.details; // 
        },
        hasVisitedHome: (state) => state.hasVisitedHome,
        getFavorites: state => state.favorites,// 
        getUserId: state => state.userId, //  ID
    }
});

export default store; //  Vuex store
