<template>
    <div class="categories-page">
        <!--  -->
        <section class="latest-article-section">
            <div class="latest-article-wrapper">
                <div class="latest-article-content">
                    <router-link :to="{
                        name: 'Article',
                        params: { id: categorieslatestArticles[0]?._id || '' },
                    }">
                        <span class="article-label"></span>
                        <h1 class="article-title">{{ categorieslatestArticles[0]?.title || '' }}</h1>
                        <p class="article-brief">{{ categorieslatestArticles[0]?.BriefIntroduction }}</p>
                        <div class="article-meta">
                            <div class="article-author">
                                <img :src="getImageUrl(authorAvatars[categorieslatestArticles[0]?.user], 'UserImg')" alt="">
                                <span>{{ categorieslatestArticles[0]?.authorName }}</span>
                            </div>
                            <div class="article-stats">
                                <span><i class="el-icon-view"></i> {{ categorieslatestArticles[0]?.viewCount || 0 }}</span>
                                <span><i class="el-icon-star-off"></i> {{ categorieslatestArticles[0]?.favoriteCount || 0 }}</span>
                            </div>
                        </div>
                    </router-link>
                </div>
                <div class="latest-article-image">
                    <img :src="getImageUrl(categorieslatestArticles[0]?.coverImage, 'uploads')" alt="">
                    <div class="image-overlay"></div>
                </div>
            </div>
        </section>

        <!--  -->
        <section class="articles-section">
            <div class="section-header">
                <h2></h2>
                <div class="section-filters">
                    <el-select v-model="selectedCategory" placeholder="" clearable @change="handleCategoryChange">
                        <el-option label="" value=""></el-option>
                        <el-option v-for="category in categories" :key="category._id" :label="category.name" :value="category._id">
                            <span class="category-option">
                                <i :class="getCategoryIcon(category.name)"></i>
                                <span class="category-name">{{ category.name }}</span>
                            </span>
                        </el-option>
                    </el-select>
                </div>
            </div>

            <div class="articles-grid">
                <div v-if="isLoading" class="loading-state">
                    <i class="el-icon-loading"></i>
                    <p>...</p>
                </div>
                <div v-else-if="error" class="error-state">
                    <i class="el-icon-warning"></i>
                    <p>{{ error }}</p>
                </div>
                <div v-else-if="filteredArticles.length === 0" class="empty-state">
                    <div class="empty-state-content">
                        <i class="el-icon-document-delete empty-icon"></i>
                        <h3></h3>
                        <p><br></p>
                        <el-button type="primary" class="browse-button" @click="selectedCategory = ''">
                            <i class="el-icon-discover"></i>
                            
                        </el-button>
                    </div>
                </div>
                <template v-else>
                    <article v-for="article in paginatedArticles" :key="article._id" class="article-card">
                        <router-link :to="{ name: 'Article', params: { id: article._id } }">
                            <div class="article-image">
                                <img :src="getImageUrl(article.coverImage, 'uploads')" alt="">
                                <div class="article-overlay">
                                    <span class="read-more"></span>
                                </div>
                            </div>
                            <div class="article-content">
                                <div class="article-meta">
                                    <span class="article-category">{{ getArticleCategories(article) }}</span>
                                    <span class="article-date">{{ formatDate(article.createdAt) }}</span>
                                </div>
                                <h3 class="article-title">{{ article.title }}</h3>
                                <p class="article-description">{{ article.BriefIntroduction }}</p>
                                <div class="article-footer">
                                    <div class="article-author">
                                        <img :src="getImageUrl(authorAvatars[article.user], 'UserImg')" alt="">
                                        <span>{{ article.authorName }}</span>
                                    </div>
                                    <div class="article-stats">
                                        <span><i class="el-icon-view"></i> {{ article.viewCount || 0 }}</span>
                                        <span><i class="el-icon-star-off"></i> {{ article.favoriteCount || 0 }}</span>
                                    </div>
                                </div>
                            </div>
                        </router-link>
                        <div class="article-actions">
                            <span v-if="userInformation" 
                                  class="favorite-button" 
                                  @click.stop="toggleFavorite(article._id)">
                                <i :class="article.isFavorite ? 'el-icon-star-on' : 'el-icon-star-off'"></i>
                            </span>
                        </div>
                    </article>
                </template>
            </div>

            <!--  -->
            <div class="pagination-wrapper">
                <el-pagination
                    background
                    layout="prev, pager, next"
                    :total="categoriesarticles.length"
                    :page-size="pageSize"
                    :current-page="currentPage"
                    @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </section>
    </div>
</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex';
export default {
    data() {
        return {
            categoriesarticles: [], // 
            categorieslatestArticles: [], // 
            authorAvatars: {}, // key ID
            userInformation: '', // ID
            currentPage: 1, // 
            pageSize: 6, // 
            favorites: [], // 
            isLoading: false, // 
            error: null, // 
            selectedCategory: '', // 
            categories: [] // 
        };
    },
    components: {},
    computed: {
        // 
        paginatedArticles() {
            const start = (this.currentPage - 1) * this.pageSize
            const end = start + this.pageSize
            return this.filteredArticles.slice(start, end)
        },
        filteredArticles() {
            if (!this.selectedCategory) {
                return this.categoriesarticles;
            }
            return this.categoriesarticles.filter(article => {
                return article.categories && article.categories.some(cat => cat._id === this.selectedCategory);
            });
        },
    },
    watch: {},
    methods: {
        ...mapActions(["VuexloadFavorites"]),
        getImageUrl(imageName, type = 'uploads') {
            if (!imageName) {
                //  imageName  null 
                return null;
            }

            // 
            const isDevelopment = process.env.NODE_ENV === 'development';

            // 
            let imageUrl;
            if (isDevelopment) {
                //  localhost:3000
                imageUrl = `http://localhost:3000/${type}/${imageName}`;
            } else {
                // 
                imageUrl = `${window.location.origin}/${type}/${imageName}`;
            }

            // console.log('', imageUrl);
            return imageUrl;
        },
        async categoriesfetchArticles() {
            this.isLoading = true;
            this.error = null;
            try {
                // GET
                const response = await axios.get('/api/articles')
                this.categoriesarticles = response.data // 
                this.categorieslatestArticles = this.categoriesarticles.slice(-1) // 
                
                // 
                await Promise.all(this.categoriesarticles.map(article => {
                    if (article.user) {
                        return this.fetchAuthorAvatar(article.user)
                    }
                    return Promise.resolve()
                }))
            } catch (error) {
                this.error = ''
                console.error('Error fetching articles:', error)
            } finally {
                this.isLoading = false
            }
        },
        // 
        async fetchAuthorAvatar(userId) {
            try {
                const userResponse = await axios.get(`/api/public-user-info/${userId}`)
                this.$set(this.authorAvatars, userId, userResponse.data.avatar) // 


            } catch (error) {
                console.error('Error fetching user avatar:', error)
            }
        },
       
        // 
        handlePageChange(page) {
            this.currentPage = page
        },
        async getuser() {
            if (!localStorage.getItem('token')) {//
                return;
            }
            const response = await axios.get('/api/user', {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
            })

            this.userInformation = response.data._id //  response.data  _id
            // console.log('id', response.data);

            // console.log('', this.userInformation);
        },

        async toggleFavorite(articleId) {
            const userId = this.userInformation //  ID
            // console.log('id', userId);

            try {
                // 
                const response = await axios.get(`/api/favorites/${userId}/${articleId}`)

                console.log('', response);

                const isFavorite = response.data // 
                console.log('', isFavorite);

                if (isFavorite) {
                    // 
                    await axios.delete(`/api/favorites`, {
                        data: { userId, articleId },
                    })
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                    const article = this.categoriesarticles.find((a) => a._id === articleId)
                    if (article) {
                        article.isFavorite = false // 
                    }
                    this.VuexloadFavorites()
                } else {
                    // 
                    await axios.post(`/api/favorites`, {
                        userId,
                        articleId,
                    })

                    const article = this.categoriesarticles.find((a) => a._id === articleId)
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    console.log('')
                    // console.log('', article);

                    if (article) {
                        article.isFavorite = true // 
                    }
                    this.VuexloadFavorites()
                }
            } catch (error) {
                console.error(':', error)
            }
        },
        //idididid
        async loadFavorites() {
            console.log('loadFavorites');
            

            if (!localStorage.getItem('token')) {
                return;
            }
            const response = await axios.get('/api/user', {
                //
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
            })

            const LoaduserId = response.data._id //  ID

            try {
                const response = await axios.get(`/api/favorites/${LoaduserId}`)

                this.favorites = response.data; // 
                console.log('', this.favorites);
              
                // 
                if (!Array.isArray(this.favorites)) {
                    console.error("", this.favorites);
                    return;
                }

                this.categoriesarticles.forEach((article) => {
                    //  this.favorites  article._id 
                    article.isFavorite = this.favorites.some((fav) => {
                        // fav.articleIdnull
                        if (!fav.articleId) {
                            return false;
                        }
                        // console.log(":", fav.articleId._id);  //  articleId
                        // console.log(" _id:", article._id);      //  _id
                        return String(fav.articleId._id) === String(article._id);
                    });

                    // 
                    // console.log("", article.isFavorite, article._id);
                });


            } catch (error) {
                console.error(':', error)
            }
        },

        formatDate(date) {
            if (!date) return '';
            return new Date(date).toLocaleDateString('zh-CN', {
                year: 'numeric',
                month: 'long',
                day: 'numeric'
            });
        },
        getArticleCategories(article) {
            if (!article.categories) return '';
            return article.categories.map(cat => cat.name).join(', ');
        },
        async fetchCategories() {
            try {
                const response = await axios.get('/api/categories');
                this.categories = response.data;
            } catch (error) {
                console.error('Error fetching categories:', error);
            }
        },
        getCategoryIcon(categoryName) {
            const iconMap = {
                '': 'el-icon-cpu',
                '': 'el-icon-coffee-cup',
                '': 'el-icon-notebook-2',
                '': 'el-icon-share',
                '': 'el-icon-more'
            };
            return iconMap[categoryName] || 'el-icon-folder';
        },
        handleCategoryChange() {
            this.currentPage = 1; // 
        }
    },

    created() {
        this.getuser()
        this.loadFavorites()
        this.categoriesfetchArticles()
        this.fetchCategories()
    },

}
</script>

