<template>
  <div class="user-page">
    <!--  -->
    <div class="user-header">
      <div class="user-banner">
        <img src="../assets/1.jpg" alt="banner">
        <div class="user-avatar">
          <img :src="getImageUrl(user.avatar, 'UserImg')" alt="" @click="openDialog">
        </div>
      </div>
      <div class="user-info">
        <div class="user-details">
          <div class="username-container">
            <h2>{{ user.username }}</h2>
            <el-button type="text" icon="el-icon-edit" @click="openEditUsernameDialog"></el-button>
          </div>
          <p>{{ user.email }}</p>
          <el-button type="text" class="change-password-btn" @click="openChangePasswordDialog"></el-button>
        </div>
        <el-button type="primary" @click="LU_logout" style="margin-left: auto;"></el-button>
      </div>
    </div>

    <!--  -->
    <div class="user-content">
      <!--  -->
      <div class="user-sidebar">
        <el-card class="statistics-card">
          <div class="stat-item">
            <div class="stat-value">{{ totalViews }}</div>
            <div class="stat-label"></div>
          </div>
          <div class="stat-item">
            <div class="stat-value">{{ userArticles.length }}</div>
            <div class="stat-label"></div>
          </div>
          <el-button type="primary" class="create-article-btn" @click="openCreateArticleDialog">
            <i class="el-icon-edit"></i> 
          </el-button>
        </el-card>

        <el-card class="menu-card">
          <el-tabs v-model="activeName">
            <el-tab-pane label="" name="first">
              <div class="tab-content">
                <div v-if="userArticles && userArticles.length > 0" class="articles-list">
                  <div v-for="article in userArticles" :key="article._id" class="article-item">
                    <router-link :to="{ name: 'Article', params: { id: article._id }}">
                      <div class="article-content">
                        <img v-if="article.coverImage" :src="getImageUrl(article.coverImage, 'uploads')" alt="">
                        <div class="article-info">
                          <h4>{{ article.title }}</h4>
                          <p>{{ article.BriefIntroduction || '' }}</p>
                          <div class="article-meta">
                            <span><i class="el-icon-view"></i> {{ article.viewCount || 0 }}</span>
                            <span><i class="el-icon-star-off"></i> {{ article.favoriteCount || 0 }}</span>
                            <el-button type="danger" size="mini" icon="el-icon-delete" @click.prevent="confirmDelete(article._id)"></el-button>
                          </div>
                        </div>
                      </div>
                    </router-link>
                  </div>
                </div>
                <div v-else class="no-data"></div>
              </div>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <div class="tab-content">
                <div v-if="favorites && favorites.length > 0" class="favorites-list">
                  <div v-for="actions in favorites" :key="actions.articleId?._id || actions._id" class="article-item">
                    <!--  -->
                    <router-link v-if="actions.articleId?._id" :to="{ name: 'Article', params: { id: actions.articleId._id }}">
                      <div class="article-content">
                        <img v-if="actions.articleId?.coverImage" :src="getImageUrl(actions.articleId.coverImage, 'uploads')" alt="">
                        <div class="article-info">
                          <h4>{{ actions.articleId?.title || '' }}</h4>
                          <p>{{ actions.articleId?.BriefIntroduction || '' }}</p>
                          <div class="article-meta">
                            <span><i class="el-icon-view"></i> {{ actions.articleId?.viewCount || 0 }}</span>
                            <span><i class="el-icon-star-off"></i> {{ actions.articleId?.favoriteCount || 0 }}</span>
                            <el-button type="warning" size="mini" icon="el-icon-star-off" @click.prevent="confirmUnfavorite(actions._id)"></el-button>
                          </div>
                        </div>
                      </div>
                    </router-link>
                    <!--  -->
                    <div v-else class="article-content invalid-favorite">
                      <div class="article-info">
                        <h4></h4>
                        <p></p>
                        <div class="article-meta">
                          <el-button type="danger" size="mini" icon="el-icon-delete" @click.prevent="confirmUnfavorite(actions._id)"></el-button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div v-else class="no-data"></div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-card>
      </div>

      <!--  -->
      <div class="user-main">
        <el-card class="recent-view-card">
          <div slot="header">
            <span></span>
          </div>
          <div v-if="viewedArticles && viewedArticles.length > 0" class="recent-view-content">
            <div v-for="article in viewedArticles" :key="article.articleId" class="article-item">
              <router-link :to="{ name: 'Article', params: { id: article.articleId }}">
                <div class="article-content">
                  <img v-if="article.coverImage" :src="getImageUrl(article.coverImage, 'uploads')" alt="">
                  <div class="article-info article-info-title">
                    <h4>{{ article.title || '' }}</h4>
                    <div class="article-meta">
                      <span><i class="el-icon-view"></i> {{ article.viewCount || 0 }}</span>
                      <span><i class="el-icon-star-off"></i> {{ article.favoriteCount || 0 }}</span>
                      <span class="reading-progress">
                        <el-progress 
                          :percentage="calculateProgress(article.scrollPosition, article.maxScrollPosition)" 
                          :format="percent => `${percent}%`"
                          :stroke-width="18"
                          :show-text="true"
                          :color="['#67C23A', '#E6A23C', '#F56C6C']"
                          :text-inside="true"
                          class="custom-progress"
                        ></el-progress>
                      </span>
                      <span class="view-time">{{ new Date(article.lastViewedAt).toLocaleString() }}</span>
                    </div>
                  </div>
                </div>
              </router-link>
            </div>
          </div>
          <div v-else class="no-data"></div>
        </el-card>
      </div>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="300px">
      <el-upload action="/api/update-avatar" :headers="headers" name="avatar" list-type="picture-card"
        :on-success="handleSuccess" :on-error="handleError">
        <i class="el-icon-plus"></i>
      </el-upload>
      <p></p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="deleteDialogVisible"
      width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialogVisible = false"></el-button>
        <el-button type="danger" @click="deleteArticle"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="unfavoriteDialogVisible"
      width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="unfavoriteDialogVisible = false"></el-button>
        <el-button type="warning" @click="unfavoriteArticle"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editUsernameDialogVisible"
      width="400px"
      custom-class="custom-dialog"
      :close-on-click-modal="false"
    >
      <el-form
        :model="usernameForm"
        :rules="usernameRules"
        ref="usernameForm"
        label-position="top"
        class="custom-form"
      >
        <el-form-item label="" prop="newUsername">
          <el-input
            v-model="usernameForm.newUsername"
            placeholder=""
            prefix-icon="el-icon-user"
            maxlength="20"
            show-word-limit
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button plain @click="editUsernameDialogVisible = false"></el-button>
        <el-button type="primary" @click="submitUsername"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="changePasswordDialogVisible"
      width="400px"
      custom-class="custom-dialog"
      :close-on-click-modal="false"
    >
      <el-form
        :model="passwordForm"
        :rules="passwordRules"
        ref="passwordForm"
        label-position="top"
        class="custom-form"
      >
        <el-form-item label="" prop="currentPassword">
          <el-input
            type="password"
            v-model="passwordForm.currentPassword"
            placeholder=""
            prefix-icon="el-icon-lock"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPassword">
          <el-input
            type="password"
            v-model="passwordForm.newPassword"
            placeholder=""
            prefix-icon="el-icon-key"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input
            type="password"
            v-model="passwordForm.confirmPassword"
            placeholder=""
            prefix-icon="el-icon-key"
            show-password
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button plain @click="changePasswordDialogVisible = false"></el-button>
        <el-button type="primary" @click="submitPassword"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="createArticleDialogVisible"
      width="80%"
      :fullscreen="isMobile"
      custom-class="create-article-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="articleForm" :rules="articleRules" ref="articleForm" label-position="top">
        <el-form-item label="" prop="title">
          <el-input v-model="articleForm.title" placeholder="" maxlength="100" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="categories">
          <el-select v-model="articleForm.categories" placeholder="" style="width: 100%">
            <el-option v-for="category in categories" :key="category._id" :label="category.name" :value="category._id">
              <span class="category-option">
                <i :class="getCategoryIcon(category.name)"></i>
                <span class="category-name">{{ category.name }}</span>
                <span class="category-desc">{{ category.description }}</span>
              </span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="coverImage">
          <el-upload 
            class="upload-demo" 
            action="" 
            :file-list="fileList" 
            :on-change="handleFileUpload" 
            :auto-upload="false" 
            :limit="1" 
            :on-exceed="handleExceed">
            <el-button></el-button>
            <div slot="tip" class="el-upload__tip"></div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="BriefIntroduction">
          <el-input
            type="textarea"
            v-model="articleForm.BriefIntroduction"
            :rows="3"
            placeholder=""
            maxlength="200"
            show-word-limit>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <div class="editor-container">
            <quill-editor v-model="articleForm.content"
              :options="editorOptions"
              class="quill-editor"
              @text-change="onEditorChange">
            </quill-editor>
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="createArticleDialogVisible = false"></el-button>
        <el-button type="primary" @click="submitArticle" :loading="submitting"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <Footer />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import router from '@/router';
import Footer from './Footer.vue';
import { VueEditor } from 'vue2-editor';
export default {
  components: {
    Footer,
    VueEditor
  },
  data() {
    return {
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`, //  localStorage  token
      },
      user: "",
      dialogVisible: false, // 
      deleteDialogVisible: false, // 
      unfavoriteDialogVisible: false, // 
      articleToDelete: null, // ID
      favoriteToRemove: null, // ID
      // 
      favorites: [],
      // 
      fileList: [],
      editorOptions: {
        theme: "snow",
        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ align: [] }],
            ["clean"],
            ["image"], // 
          ],
        },
      },
      articleForm: {
        title: "",
        BriefIntroduction: "",
        content: "",
        categories: "", // ID
        coverImage: null,
      },
      userArticles: [],
      // 
      viewedArticles: [],
      isMobile: false,// 
      activeName: 'first',
      totalViews: 0, // 
      // 
      editUsernameDialogVisible: false,
      usernameForm: {
        newUsername: ''
      },
      usernameRules: {
        newUsername: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ]
      },
      // 
      changePasswordDialogVisible: false,
      passwordForm: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      // 
      createArticleDialogVisible: false,
      submitting: false,
     
      categories: [], // 
      articleRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 100, message: ' 2  100 ', trigger: 'blur' }
        ],
        category: [
          { required: true, message: '', trigger: 'change' }
        ],
        BriefIntroduction: [
          { required: true, message: '', trigger: 'blur' },
          { max: 200, message: '200', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      customToolbar: [
        ['bold', 'italic', 'underline'],
        [{ 'list': 'ordered'}, { 'list': 'bullet' }],
        ['image', 'code-block'],
        [{ 'color': [] }, { 'background': [] }],
        [{ 'align': [] }]
      ],
      passwordRules: {
        currentPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: (rule, value, callback) => {
            if (value !== this.passwordForm.newPassword) {
              callback(new Error(''));
            } else {
              callback();
            }
          }, trigger: 'blur' }
        ]
      }
    };
  },
  props: {
    lastViewedArticleZi: {
      type: Object,
    },
  },
  computed: {
    ...mapGetters(['getUser', 'isLoggedIn']),

  },
  created() {
    // store
    this.user = this.getUser;
    
    // store
    if (this.isLoggedIn && (!this.user || !this.user._id)) {
      axios.get('/api/user', {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      }).then(response => {
        const userDetails = response.data;
        // store
        this.$store.dispatch('updateUser', { 
          token: localStorage.getItem('token'), 
          details: userDetails 
        });
        this.user = userDetails;
        // 
        this.initializeUserData();
      }).catch(error => {
        console.error(':', error);
        this.$message.error('');
      });
    } else {
      // 
      this.initializeUserData();
    }
  },

  async mounted() {
    await this.loadUserArticles(); // 
    await this.loadViewedArticles(); // 
    await this.$store.dispatch('VuexloadFavorites'); // 
    this.favorites = this.$store.getters.getFavorites; // Vuex
  },


  methods: {
    // 
    async initializeUserData() {
      if (!this.user || !this.user._id) {
        console.error('');
        return;
      }

      const token = localStorage.getItem('token');
      if (!token) {
        this.$router.push('/login');
        return;
      }

      const headers = { Authorization: `Bearer ${token}` };

      // 
      try {
        const response = await axios.get(`/api/users/${this.user._id}/articles`);
        this.userArticles = response.data;
        // 
        this.totalViews = this.userArticles.reduce((total, article) => total + (article.viewCount || 0), 0);
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }

      // 
      try {
        const response = await axios.get(`/api/favorites/${this.user._id}`, { headers });
        this.favorites = response.data;
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }

      // 
      try {
        const response = await axios.get('/api/user/viewed-articles', { headers });
        this.viewedArticles = response.data;
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },

    // 
    async loadUserArticles() {
      try {
        if (!this.user._id) return;
        
        const response = await axios.get(`/api/users/${this.user._id}/articles`);
        this.userArticles = response.data;

      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },

    // 
    async loadViewedArticles() {
      try {
        const response = await axios.get('/api/user/viewed-articles', {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        });
        this.viewedArticles = response.data;
      } catch (error) {
        console.error(':', error);
      }
    },

    LU_logout() {
      this.$emit('call-parent')
    },
    getImageUrl(imageName, type) {
      if (!imageName) {
        return null;
      }

      const baseUrl = window.location.origin;
      
      // type
      const imagePath = type === 'UserImg' ? `UserImg/${imageName}` : `uploads/${imageName}`;
      const url = new URL(imagePath, baseUrl);


      return url.toString();
    },

    // 
    openDialog() {
      this.dialogVisible = true; // 
    },
    handleSuccess(response, file) {
      this.$message.success('');
      this.dialogVisible = false; // 
    },
    async fetchCategories() {
      try {
        const response = await axios.get("/api/categories");
        this.categories = response.data;
      } catch (error) {
        console.error("Error fetching categories:", error);
        this.$message.error("");
      }
    },
    getCategoryIcon(categoryName) {
      const iconMap = {
        '': 'el-icon-cpu',
        '': 'el-icon-coffee-cup',
        '': 'el-icon-notebook-2',
        '': 'el-icon-share',
        '': 'el-icon-folder'
      };
      return iconMap[categoryName] || 'el-icon-folder';
    },
    handleError(err) {
      this.$message.error('');
    },

    // 
    confirmDelete(articleId) {
      this.articleToDelete = articleId;
      this.deleteDialogVisible = true;
    },

    // 
    async deleteArticle() {
      try {
        await axios.delete(`/api/articles/${this.articleToDelete}`, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        });
        
        this.$message.success('');
        this.deleteDialogVisible = false;
        // 
        this.loadUserArticles();
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },

    // 
    confirmUnfavorite(favoriteId) {
      this.favoriteToRemove = favoriteId;
      this.unfavoriteDialogVisible = true;
    },

    // 
    async unfavoriteArticle() {
      try {
        const userId = this.user._id;
        const favorite = this.favorites.find(f => f._id === this.favoriteToRemove);
        
        // 
        if (!favorite) {
          throw new Error('');
        }
        
        // articleIdnull
        if (!favorite.articleId) {
          // 
          await axios.delete('/api/favorites', {
            data: { favoriteId: favorite._id }
          });
          
          this.$notify({
            title: '',
            message: '',
            type: 'warning'
          });
          this.unfavoriteDialogVisible = false;
          // 
          this.loadFavorites();
          return;
        }
        
        // 
        const articleId = favorite.articleId._id;

        if (!userId || !articleId) {
          throw new Error('IDID');
        }

        await axios.delete('/api/favorites', {
          data: { userId, articleId }
        });
        
        this.$notify({
          title: '',
          message: '',
          type: 'warning'
        });
        this.unfavoriteDialogVisible = false;
        // 
        this.loadFavorites();
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },
    // PC
    // checkDeviceType() {
    //   const width = window.innerWidth;
    //   this.isMobile = width <= 768; // 

    //   // PC
    //   if (!this.isMobile) {
    //     this.$router.push('/Index/Home');
    //   }
    // },
    // 
    async loadUserArticles() {
      try {
        if (!this.user._id) return;
        
        const response = await axios.get(`/api/users/${this.user._id}/articles`);
        this.userArticles = response.data;

      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },

    //
    async loadFavorites() {
      if (!localStorage.getItem('token')) {
        return;
      }
      const response = await axios.get('/api/user', {
        //
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })

      const LoaduserId = response.data._id //  ID

      try {
        const response = await axios.get(`/api/favorites/${LoaduserId}`)

        this.favorites = response.data; // 
        
        // 
        if (!Array.isArray(this.favorites)) {
            console.error("", this.favorites);
            return;
        }
        
        // 

      } catch (error) {
        console.error(':', error)
      }
    },
    // 
    async loadTotalViews() {
      try {
        if (!this.user._id) return;
        
        const response = await axios.get(`/api/users/${this.user._id}/total-views`);
        this.totalViews = response.data.totalViews;

      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },

    // 
    async loadViewedArticles() {
      try {
        const response = await axios.get('/api/user/viewed-articles', {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        });
        this.viewedArticles = response.data.sort((a, b) => new Date(b.lastViewedAt) - new Date(a.lastViewedAt));

      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },

    // 
    calculateProgress(scrollPosition, maxScrollPosition) {
      if (!maxScrollPosition) return 0;
      const progress = (scrollPosition / maxScrollPosition) * 100;
      return Math.min(Math.round(progress), 100);
    },

    // 
    openCreateArticleDialog() {
      this.createArticleDialogVisible = true;
      this.$nextTick(() => {
        this.$refs.articleForm?.resetFields();
        this.fetchCategories(); // 
      });
    },

    // 
    async fetchCategories() {
      try {
        const response = await axios.get("/api/categories");
        this.categories = response.data;
      } catch (error) {
        console.error("Error fetching categories:", error);
        this.$message.error('');
      }
    },

    handleExceed(files, fileList) {
      this.$message.warning('');
    },

    handleFileUpload(file, fileList) {
      // 5MB
      const maxSize = 5 * 1024 * 1024; // 5MB in bytes
      if (file.size > maxSize) {
        this.$notify({
          title: '',
          message: '5MB',
          type: 'error',
          duration: 3000
        });
        // 
        fileList.splice(fileList.indexOf(file), 1);
        return;
      }
      if (fileList.length > 1) {
        fileList.splice(0, 1);
      }
      this.articleForm.coverImage = file.raw;
      this.fileList = fileList;
    },

    // 
    onEditorChange() {
      this.$refs.articleForm?.validateField('content');
    },

    // 
    async submitArticle() {
      this.$refs.articleForm.validate(async (valid) => {
        if (!valid) return;
        
        try {
          this.submitting = true;
          const formData = new FormData();
          Object.keys(this.articleForm).forEach((key) => {
            formData.append(key, this.articleForm[key]);
          });
          formData.append('author', this.user._id);
          console.log(this.articleForm);
          const token = localStorage.getItem('token');
          const response = await axios.post('/api/articles', formData, {
            headers: {
              Authorization: `Bearer ${token}`,
              'Content-Type': 'multipart/form-data'
            }
          });

          this.$notify({
            title: '',
            message: '',
            type: 'success'
          });
          // 
          this.articleForm = {
            title: "",
            BriefIntroduction: "",
            content: "",
            categories: "",
            coverImage: null
          };
          // 
          this.fileList = [];
          this.createArticleDialogVisible = false;
          this.loadUserArticles(); // 
        } catch (error) {
          let errorMessage = '';
          if (error.response && error.response.data) {
            errorMessage = error.response.data.message || error.response.data.msg || errorMessage;
          }
          this.$notify({
            title: '',
            message: errorMessage,
            type: 'error',
            duration: 3000
          });
          console.error(':', error.response || error);
        } finally {
          this.submitting = false;
        }
      });
    },

    // 
    openEditUsernameDialog() {
      this.usernameForm.newUsername = this.user.username;
      this.editUsernameDialogVisible = true;
    },

    // 
    submitUsername() {
      this.$refs.usernameForm.validate(async (valid) => {
        if (!valid) return;
        try {
          console.log(this.usernameForm.newUsername)
          const response = await axios.put(`/api/update-username/${this.user._id}`, {
            newUsername: this.usernameForm.newUsername
          }, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`
            }
          });
          
          // 
          this.user.username = this.usernameForm.newUsername;
          this.$store.dispatch('updateUser', { details: { ...this.user } });
          
          this.$message.success('');
          this.editUsernameDialogVisible = false;
        } catch (error) {
          this.$message.error(error.response?.data?.message || '');
        }
      });
    },

    // 
    openChangePasswordDialog() {
      this.changePasswordDialogVisible = true;
      this.$nextTick(() => {
        this.$refs.passwordForm?.resetFields();
      });
    },

    // 
    submitPassword() {
      this.$refs.passwordForm.validate(async (valid) => {
        if (!valid) return;
        try {
          await axios.put(`/api/update-password/${this.user._id}`, {
            currentPassword: this.passwordForm.currentPassword,
            newPassword: this.passwordForm.newPassword
          }, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`
            }
          });
          
          this.$message.success('');
          this.changePasswordDialogVisible = false;
          
          // 
          localStorage.removeItem('token');
          this.$store.dispatch('updateUser', { token: null, details: null });
          this.$router.push('/login');
        } catch (error) {
          this.$message.error(error.response?.data?.message || '');
        }
      });
    }
  },
  mounted() {
    this.loadFavorites();
    this.loadUserArticles();
    this.loadTotalViews(); // 
    this.loadViewedArticles(); // 
    this.fetchCategories(); // 
  }
};
</script>

