<template>
    <div class="form-container">
        <h2>Sign in</h2>
        <el-form @submit.prevent="handleSubmit" class="ElFrom" :model="form" :rules="rules" ref="loginForm">
            <el-form-item prop="email">
                <el-input class="ElInp" v-model="form.email" placeholder="" prefix-icon="el-icon-message"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input class="ElInp" v-model="form.password" type="password" placeholder="" prefix-icon="el-icon-lock" show-password></el-input>
            </el-form-item>
            <el-button type="primary" class="BtnAft" @click="submitForm('loginForm')" :loading="loading" block></el-button>
        </el-form>
        <div class="divider">
            <span class="line"></span>
            <span class="text">or</span>
            <span class="line"></span>
        </div>
        <div class="signin-link">
            <small>
                ? <a href="#" @click.prevent="toggleForm"></a>
            </small>
        </div>

    </div>
</template>

<script>
import axios from 'axios'; //  axios
import { Notification } from 'element-ui';
import { mapActions } from 'vuex'; //  map Vuex actions

export default {
    props: {
        toggleForm: {
            type: Function,
            required: true
        }
    },
    data() {
        return {
            form: {
                email: '',
                password: ''
            },
            rules: {
                email: [
                    { required: true, message: '', trigger: 'blur' },
                    { type: 'email', message: '', trigger: ['blur', 'change'] }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 6, message: '6', trigger: 'blur' }
                ]
            },
            loading: false,
            theme: localStorage.getItem('theme') || 'light'
        };
    },
    methods: {
        ...mapActions(['updateUser']), //  Vuex actions
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (!valid) {
                    return false;
                }
                this.loading = true;
                try {
                    // 
                    const requestData = {
                        email: this.form.email,
                        password: this.form.password
                    };
                    // console.log(requestData);
                    // 
                    const response = await axios.post('/api/auth/login', requestData, {
                        headers: {
                            'Content-Type': 'application/json'
                        }
                    });

                    // 
                    Notification.success({
                        title: '',
                        message: '',
                        duration: 3000
                    });

                    //  token 
                    const token = response.data.token;
                    console.log('', response.data);
                    const userDetails = response.data.user; // 

                    localStorage.setItem('token', token);
                    console.log("", userDetails);

                    //  Vuex 
                    this.$store.dispatch('updateUser', { token, details: userDetails });

                    // 
                    axios.get('/api/user', {
                        headers: { Authorization: `Bearer ${token}` }
                    }).then(userResponse => {
                        // 
                        console.log("", userResponse);
                        const userDetails = userResponse.data;

                        //  Vuex 
                        this.$store.dispatch('updateUser', { token, details: userDetails });
                    }).catch(userError => {
                        console.error('Get user details error:', userError);
                    });

                    // 
                    this.$router.push('/Index'); // /Index 
                } catch (error) {
                    this.loading = false;
                    // 

                    if (error.response && error.response.data) {
                        // 
                        const errorMsg = error.response.data.msg || '';
                        Notification.error({
                            title: '',
                            message: errorMsg,
                            duration: 3000
                        });
                    } else {
                        // 
    
                        Notification.error({
                            title: '',
                            message: '',
                            duration: 3000
                        });
                    }
                }
            });
        },

    },
    mounted() {
        this.theme = localStorage.getItem('theme') || 'light';
    }
};
</script>

